@import "../../global.module";

.fundraisingPageWrapper {
  background-color: white;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100%;
  max-height: 100%;
  overflow-y: auto;
}

.period {
  font-size: 14px;
  font-weight: 400;
  font-style: italic;
  vertical-align: super;
}

.totalAmountHeader {
  span {
    font-size: 21px;
    margin-left: 7px;
  }
}

.progressBarWrapper {
  margin-top: 20px;
}

:global(.ant-progress-circle-path) {
  stroke: $yellow_color !important;
}

.swapsHeader {
  margin-top: 60px;
}

.swapsCommonWrapper {
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
}

.progressBarText {
  display: flex;
  flex-direction: column;
  color: black;
  font-size: 30px;
  line-height: 30px;

  span {
    font-size: 16px;
  }
}

.swapsSection {
  margin-top: 50px;
  margin-bottom: 25px;

  .swapCard {
    &:not(:last-of-type) {
      margin-bottom: 30px;
    }
  }

  button {
    margin-right: 15px;
  }

  table {
    width: 100%;
  }
}
